import * as React from 'react';
import { StaticImage } from 'gatsby-plugin-image';
import Layout from '../components/layout';
import Seo from '../components/seo';

const ContactPage = () => {
  return (
    <Layout pageTitle="お問い合わせ">
      <Seo
        pageTitle="お問い合わせ"
        pageDescription="月の森デザインへのお問い合わせはメールフォームをご利用ください。"
      />
      <StaticImage
        alt="お仕事、承ります。"
        src="../img/page-contact.png"
        placeholder="Blurred"
      />
      <p>※は、入力必須項目です。</p>
      <p>お名前※</p>
      <p>メールアドレス※</p>
      <p>題名※</p>
      <p>内容</p>
      <p>入力内容をご確認の上、チェックを入れてから送信してください。※</p>
    </Layout>
  )
};

export default ContactPage;
